body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #ffffff;
  }
  
  header {
    text-align: center;
    padding: 20px;
    background-color: #4caf50;
    color: rgb(14, 11, 11);
  }
  
  header h1 {
    margin: 0;
    font-size: 2rem;
  }
  
  header p {
    margin: 5px 0 0;
  }
  
  .container {
    margin: 20px auto;
    max-width: 800px;
    background: rgb(255, 255, 255);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .product {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #ddd;
    padding: 10px 0;
  }
  
  .product:last-child {
    border-bottom: none;
  }
  
  .product img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .product-details {
    flex: 1;
    margin-left: 10px;
  }
  
  .product-details h3 {
    margin: 0;
    font-size: 1.2rem;
    color: #333;
  }
  
  .product-details p {
    margin: 5px 0 0;
    font-size: 0.9rem;
    color: #5b5b5b;
  }
  
  button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button.edit {
    background-color: #2196f3;
    color: white;
  }
  
  button.delete {
    background-color: #f44336;
    color: white;
  }
  
  button.add {
    background-color: #4caf50;
    color: white;
  }
  
  button:hover {
    opacity: 0.9;
  }
  